@import "../../../assets/styles/index.scss";

.container {
  .catalog {
    @include font($size: 2rem, $height: 2.5rem);
  }
  @extend %container;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.buttonsContainer {
  width: 3rem;
  display: flex;
  align-items: center;
}
.status {
  margin: 15rem;
}

.name {
  margin: 3rem;
}

.birthday {
  margin: 3rem;
}
.headerCatalogFalse {
  display: none;
}
.headerCatalogTrue {
  display: block;
}
.hide {
  display: none;
}
.show {
  display: block;
}
